.transition button{
    background-color: black;
    color: white;
    border: 0;
    width: 110px;
    height: 34px;
}
.transition button:hover {
    background-color: rgba(29, 29, 29, 0.89);
    outline: none;
}
.transition img{
    display: block;
    width: 100%;
    box-sizing: border-box;
    border-style: none;
}